* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  list-style: none;
}

.country-list {
  font-size: 15px;
  color: black;
  text-decoration: underline 3px solid rgb(37, 37, 7);
}

.country-title {
  color: blue;
  text-decoration: underline 3px solid rgb(211, 211, 5);
}

.country-list-info {
  color: rgb(48, 134, 48);
  font-size: 25px;
}
